import Card from "./UI/Card";
import styles from "./NewItem.module.css";
import { useState } from "react";

//form alanı
const NewItem = (props) => {
  const [todoInput, setTodoInput] = useState("");
  const [dateInput, setDateInput] = useState("");
  const [isValidTodo, setIsValidTodo] = useState(true);
  const [isValidDate, setIsValidDate] = useState(true);

  const submitHandler = (event) => {
    event.preventDefault();
    if (todoInput.trim().length === 0) {
      setIsValidTodo(false);
      return;
    }
    if (dateInput.trim().length === 0) {
      setIsValidDate(false);
      return;
    }

    const todo = {
      todo: todoInput,
      date: new Date(dateInput),
      id: Math.random(),
    };
    props.updateTodos(todo);
    console.log(todo);
    setDateInput("");
    setTodoInput("");
  };
  const todoInputHandler = (event) => {
    setTodoInput(event.target.value);
    setIsValidTodo(true);
  };
  const dateInputHandler = (event) => {
    setDateInput(event.target.value);
    setIsValidDate(true);
  };
  return (
    <Card className={styles.form}>
      <form onSubmit={submitHandler}>
        <input
          className={`${styles.input} ${!isValidTodo ? styles.notValid : ""}`}
          type="text"
          placeholder="new todo"
          value={todoInput}
          onChange={todoInputHandler}
        />
        <input
          className={`${styles.input} ${!isValidDate ? styles.notValid : ""}`}
          type="date"
          value={dateInput}
          onChange={dateInputHandler}
        />
        <button type="submit">add item</button>
      </form>
    </Card>
  );
};

export default NewItem;
