import styles from "./TodoDate.module.css";

const TodoDate = (props) => {
  const month = props.date.toLocaleString("en-US", { month: "2-digit" });
  const day = props.date.toLocaleString("en-US", { day: "2-digit" });
  const year = props.date.toLocaleString("en-US", { year: "2-digit" });

  return (
    <div className={styles["todo-date"]}>
      <div className={styles["todo-date__day"]}>{day}-</div>
      <div className={styles["todo-date__month"]}>{month}-</div>
      <div className={styles["todo-date__year"]}>{year}</div>
    </div>
  );
};
export default TodoDate;
